<template>
	<div class="container space-top">
		<b-card-group deck class="mb-3" v-for="bug in bugs" v-bind:bug="bug">
	      <b-card v-bind:bg-variant="bug.variant"
	        text-variant="white"
	        class="text-center">
	        <p class="card-text">{{bug.shortDescription}}</p>

	        <router-link class="btn btn btn-outline-light btn-sm" 
	        	v-bind:to="{name: 'view-bug', params: {bug_id: bug.shortDescription}}">
	        	revisar
	        </router-link>
	    </b-card>
	    </b-card-group>
	</div>
</template>

<script>
	import { db } from '../api/firebase'
	export default {
		name: 'Bugs',
		data(){
			return {
				bugs: [],
				loading: false
			}
		},
		firestore(){
			return{
				bugs: db.collection('Bugs')
			}
		}
	}
</script>

<style>
@keyframes placeHolderShimmer{
    0%{
      background-position: -468px 0
    }
    100%{
      background-position: 468px 0
    }
  }
  .animated-background__sub {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-name: placeHolderShimmer;
    animation-name: placeHolderShimmer;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    background: #f6f7f8;
    background: #eeeeee;
    background: -webkit-gradient(linear, left top, right top, color-stop(8%, #eeeeee), color-stop(18%, #dddddd), color-stop(33%, #eeeeee));
    background: -webkit-linear-gradient(left, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    -webkit-background-size: 800px 104px;
    background-size: 800px 104px;
    height: 20px;
    width: 200px;
    position: relative;
  }
</style>


